
.slider {
  --auto-duration: 2000;
  --click-duration: 600;
  overflow: hidden;
}

.slider__list {
  display: flex;
}

.slider__item {
  width: 100%;
  flex-shrink: 0;
}

.slider__list--click-duration {
  transition: transform var(--click-duration + 'ms', 600ms) ease-in-out;
}

.slider__list--auto-duration {
  transition: transform var(--auto-duration + 'ms', 2000ms) linear;
}

.slider__arrow-forward[disabled],
.slider__arrow-back[disabled] {
  opacity: 0.5;
}

